<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light fixed-top" id="nav">
      <div class="container-fluid">
        <a class="navbar-brand js-scroll-trigger" href="Index.html">
          <img
            class="main-logo"
            style="height: 200px; width: 200px; margin-top: -90px; margin-bottom: -90px;"
            src="img/Index/logo.png"
            alt
          />
        </a>

        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarResponsive">
          <ul class="navbar-nav ml-auto" style="align-self: flex-end">
            <router-link
              class="nav-link js-scroll-trigger"
              style="color: black"
              v-for="routes in links"
              v-bind:key="routes.id"
              :to="`${routes.page}`"
            >{{ routes.text }}</router-link>

            <li class="nav-item" v-if="inicio_sesion === false">
              <a
                class="nav-link js-scroll-trigger"
                style="color: black"
                @click="showModal()"
              >Regístrate</a>
            </li>
            <b-modal style="margin:-10" ref="my-modal" hide-footer title="Regístrate">
              <NuevoUsuario @inicio="inicioSesion"></NuevoUsuario>
            </b-modal>
            <li class="dropdown" v-if="inicio_sesion === true">
              <v-menu dark offset-y open-delay="10" return-value="RegresaUnValor">
                <template v-slot:activator="{ on }">
                  <a class="nav-link js-scroll-trigger" style="color: black" v-on="on">Mi Cuenta
                  <span  style="color: black" class="caret"></span></a>
                </template>

                <router-link
                  class="nav-link js-scroll-trigger"
                  style="color: black"
                  v-for="menu in menus"
                  v-bind:key="menu.id"
                  :to="`${menu.page}`"
                >{{ menu.titulo }}</router-link>
              </v-menu>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import NuevoUsuario from "@/components/Forms/NuevoUsuario";
import RegistroCV from "@/views/RegistroCV";
import Inicio from "@/views/Inicio";
import About from "@/views/About";

export default {
  components: {
    NuevoUsuario
  },
  data() {
    return {
      vistaActual: "RegistroUser",
      inicio_sesion: false,
      dialog: false,
      brand: "Barra de Navegacion",
      menus: [
        { titulo: "Registrar CV", page: "/registro_cv"},
        { titulo: "Mi perfil" , page:"/navegador_usuario"}
      ],
      links: [
        {
          id: 0,
          text: "Inicio",
          page: "/inicio"
        }
      ]
    };
  },
  methods: {
    showModal() {
      this.$refs["my-modal"].show();
    },
    hideModal() {
      this.$refs["my-modal"].hide();
    },
    inicioSesion: function(x) {
      console.log(x);
      this.hideModal();
      this.inicio_sesion = x;
    }
  }
};
</script>
<style scoped>
.navbar {
  background-color: rgba(0, 0, 0, 0.05);
}
</style>